.card {
  display: flex;
  flex-direction: column;
  gap: 24px;
  background-color: hsl(216, 50%, 16%);
  height: 596px;
  width: 350px;
  border-radius: 15px;
  font-family: "Outfit", sans-seri;
}

.img_container {
  position: relative;
  padding: 24px 24px 0 24px;
}

.img {
  width: 302px;
  height: 302px;
  border-radius: 8px;
  z-index: 1;
}

.view_container {
  background-image: linear-gradient(
    to right,
    hsla(178, 100%, 50%, 0.4),
    hsla(178, 100%, 50%, 0.4)
  );
  width: 302px;
  height: 302px;
  top: 24px;
  left: 24px;
  position: absolute;
  border-radius: 8px;
  cursor: pointer;
  z-index: -1;
  opacity: 0;
}
.view_container_hover {
  background-image: linear-gradient(
    to right,
    hsla(178, 100%, 50%, 0.4),
    hsla(178, 100%, 50%, 0.4)
  );
  width: 302px;
  height: 302px;
  top: 24px;
  left: 24px;
  position: absolute;
  border-radius: 8px;
  cursor: pointer;
  z-index: 9;
  opacity: 1;
  transition: opacity 0.5s;
}

.view_img {
  position: absolute;
  width: 44px;
  height: 44px;
  top: calc(50% - 22px);
  left: calc(50% - 22px);
  z-index: -1;
  opacity: 0;
  cursor: pointer;
}

.view_img_hover {
  position: absolute;
  width: 44px;
  height: 44px;
  top: calc(50% - 22px);
  left: calc(50% - 22px);
  z-index: 10;
  opacity: 1;
  cursor: pointer;
  transition: opacity 0.5s;
}

.title_container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.title {
  font-size: 22px;
  font-weight: 600;
  margin-left: 24px;
  line-height: 27px;
  cursor: pointer;
  transition: 0.5s;
}

.title:hover {
  color: hsl(178, 100%, 50%);
}

.description {
  color: hsl(215, 51%, 70%);
  font-size: 18px;
  line-height: 26px;
  padding-left: 24px;
}

.price_container {
  display: flex;
  justify-content: space-between;
  padding: 0 24px;
}

.icon_eth {
  width: 11px;
  height: 20px;
  color: hsl(178, 100%, 50%);
}

.text_eth {
  font-size: 16px;
  font-weight: 600;
  line-height: 20.16px;
  color: hsl(178, 100%, 50%);
}

.icon_clock {
  width: 13px;
  height: 13px;
  color: hsl(215, 51%, 70%);
}

.text_clock {
  font-size: 16px;
  font-weight: 400;
  line-height: 20.16px;
  color: hsl(215, 51%, 70%);
}

.icon_container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 7px;
}

.author_container {
  display: flex;
  flex-direction: column;
  gap: 16px;
  padding-left: 24px;
}

.avatar {
  width: 31px;
  height: 31px;
  border: 2px solid hsl(0, 0%, 100%);
  border-radius: 50%;
}

.avatar_text {
  font-size: 16px;
  font-size: 400;
  line-height: 20.1px;
  color: hsl(215, 51%, 70%);
}

.author_name {
  margin-left: 5px;
  color: hsl(0, 0%, 100%);
  cursor: pointer;
  transition: 0.5s;
}

.author_name:hover {
  color: hsl(178, 100%, 50%);
}

.line {
  width: 302px;
  height: 1px;
  background-color: #2e405a;
  border: none;
}

.author {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 16px;
}

@media (max-width: 375px) {
  .card {
    height: 543px;
    width: 327px;
    gap: 0;
  }

  .img {
    width: 278px;
    height: 278px;
    padding-bottom: 24px;
  }

  .view_container,
  .view_container_hover {
    width: 278px;
    height: 278px;
  }

  .title_container {
    gap: 12px;
    margin-right: 26px;
    padding-bottom: 16px;
  }

  .price_container {
    padding-right: 26px;
    padding-bottom: 16px;
  }
}
